import Axios from 'axios'
import { call, delay, fork, take, takeEvery, takeLatest, put } from 'redux-saga/effects'
import { ADD_TASK_API, CHECK_TASK_API, DELETE_TASK_API, GET_TASKLIST_API, GET_TASK_API, REJECT_TASK_API } from '../constants/ToDoListConst';
import { toDoListService } from '../../services/ToDoListService'
import { STATUS_CODE } from '../../util/constants/settingSystem';
import { DISPLAY_LOADING, HIDE_LOADING } from '../constants/LoadingConst';
/*redux 2 loi action: 
    Loi 1: action => object (action thng)
    Loi 2: action => function (thng dng  x l api hoc gi cc action khc )
*/

/*
    01/01/2020 Khi vit chc nng getTask
    Action saga ly danh sch task t api 
*/
function* getTaskApiAction(action) {
    //put ging dispatch action
    yield put({
        type: DISPLAY_LOADING
    })
    try {
        let { data, status } = yield call(toDoListService.getTaskApi)
        yield delay(300);
        if (status === STATUS_CODE.SUCCESS) {
            //Sau khi ly gi tr thnh cng dng put (ging dispatch bn thunk) 
            yield put({
                type: GET_TASK_API,
                taskList: data
            });
        } else {
            console.log('error')
        }
    }
    catch (err) {
        console.log('err')
    }
    yield put({
        type: HIDE_LOADING
    })

}

export function* theoDoiActionGetTaskApi() {
    yield takeLatest(GET_TASKLIST_API, getTaskApiAction)
}

/*
    01/01/2020 Khi vit chc nng addTask
    Action saga nghip v thm task
*/

function* addTaskApiAction(action) {
    const { taskName } = action;
    //Gi api
    try {
        const { data, status } = yield call(() => { return toDoListService.addTaskApi(taskName) });
        if (status === STATUS_CODE.SUCCESS) {
            yield put({
                type: GET_TASKLIST_API
            })
        }

    } catch (err) {
        console.log(err);
    }
    //Hin th loading
    //thnh cng th load li task = cch gi li action saga load tasklist
}

export function* theoDoiActionAddTaskApi() {
    yield takeLatest(ADD_TASK_API, addTaskApiAction)
}

/*
    01/01/2020 Khi vit chc nng deleteTask
    Action saga nghip v xa task
*/

function* deleteTaskApi(action) {
    console.log(action)
    const { taskName } = action;
    try {
        //Gi api deletetask
        const { data, status } = yield call(() => {
            return toDoListService.deleteTaskApi(taskName);
        });

        if (status === STATUS_CODE.SUCCESS) {
            //Nu thnh cng th gi li action GET_TASKLIST_API(action saga thc thi)
            yield put({
                type: GET_TASKLIST_API,
            })
        }

    } catch (err) {
        console.log(err);
    }
}



export function* theoDoiActionDeleteTask() {
    yield takeLatest(DELETE_TASK_API, deleteTaskApi)
}



/*
    01/01/2020 Khi vit chc nng deleteTask
    Action saga thc hin nghip v done task
*/

function* checkDoneTaskApi(action) {
    const { taskName } = action;

    try {
        const { data, status } = yield call(() => {
            return toDoListService.checkDoneTask(taskName);
        });
        if (status === STATUS_CODE.SUCCESS) {
            yield put({
                type: GET_TASKLIST_API
            })
        }
    } catch (error) {
        console.log(error);
    }

}



export function* theoDoiDoneTask() {
    yield takeLatest(CHECK_TASK_API, checkDoneTaskApi)
}



/*
    01/01/2020 Khi vit chc nng deleteTask
    Action saga thc hin nghip v reject task
*/
export function* rejectTaskApi(action) {
    const { taskName } = action;

    try{
        const {data,status} = yield call (()=>{
            return toDoListService.rejectTask(taskName);
        })
        
        if(status === STATUS_CODE.SUCCESS) {
            yield put ({
                type:GET_TASKLIST_API
            })
        }
    }catch(err) {
        console.log(err)
    }


}


export function* theoDoiRejectTask() {
    yield takeLatest(REJECT_TASK_API, rejectTaskApi)
}

