import Axios from "axios";
import Profile from "../../pages/Profile/Profile";
import { GET_TASK_API } from "../constants/ToDoListConst";


//Action c 2 loi
//Action thc thi ngay lm thay i reducer (action 1)
//Action phi thc hin x l ri mi gi action 1 thc thi (async action)

export const getTaskListApi = () => {

    //Tin x l d liu => x l function 
    return async dispatch => {

        try {
            let { data, status, ...res } = await Axios({
                url: 'http://svcy.myclass.vn/api/ToDoList/GetAllTask',
                method: 'GET'
            });
            if (status === 200) {
                dispatch({
                    type: GET_TASK_API,
                    taskList: data
                })
            }
        } catch (err) {
            console.log(err.response.data)
        }

        // console.log('promise',res.data)
        // promise.then((result) => {
        //     console.log(result.data);
        //     //Nu gi api ly v kt qu thnh cng 
        //     //=> set li state ca component
        //     dispatch({
        //         type: GET_TASK_API,
        //         taskList: result.data
        //     })

        //     console.log('thnh cng')
        // });


        // promise.catch((err) => {
        //     console.log('tht bi')
        //     console.log(err.response.data)
        // });
    }
}



export const addTaskApi = (taskName) => {
    return async dispatch => {

        try {
            //X l trc khi dispatch
            let {status,data} = await Axios({
                url: 'http://svcy.myclass.vn/api/ToDoList/AddTask',
                method: 'POST',
                data: { taskName: taskName }
            });
            // console.log('res',res)
            if (status === 200) {
                dispatch(getTaskListApi())
            }
        }catch(err){
            console.log(err.response.data);
        }
        // //X l thnh cng
        // promise.then(result => {
        //     // alert(result.data);
        //     dispatch(getTaskListApi());

        // })

        // //X l tht bi
        // promise.catch(errors => {
        //     alert(errors.response.data)

        // })


    }
}


export const deleteTaskApi = (taskName) => {
    return dispatch => {
        let promise = Axios({
            url: `http://svcy.myclass.vn/api/ToDoList/deleteTask?taskName=${taskName}`,
            method: 'DELETE'
        });

        promise.then(result => {
            //Sau khi thc hin api gi phng thc dispatchAction get TaskListApi  load li task
            dispatch(getTaskListApi())
        });

        promise.catch(errors => {
            alert(errors.response.data)
        })
    }
}

export const checkTaskApi = (taskName) => {
    return dispatch => {
        let promise = Axios({
            url: `http://svcy.myclass.vn/api/ToDoList/doneTask?taskName=${taskName}`,
            method: 'PUT'
        });

        promise.then(res => {
            dispatch(getTaskListApi())
        });

        promise.catch(err => {
            alert(err.response.data);
        })
    }
}


export const rejectTaskApi = (taskName) => {
    return dispatch => {
        let promise = Axios({
            url: `http://svcy.myclass.vn/api/ToDoList/rejectTask?taskName=${taskName}`,
            method: 'PUT'
        });

        promise.then(res => {
            alert(res.data);
            dispatch(getTaskListApi());
        });

        promise.catch(err => {
            alert(err.response.data);
        })
    }
}




